import React from 'react';

const Return = React.memo(
    () =>{
        return (
            <main className='main main-about'>
                <div className='container'>
                    <div className='row'>
                        <div className='col-12'>
                            <div className='recovery-wrap'>
                                <div className='recovery-title'>
                                    <h1>Порядок и условия возврата денежных средств</h1>
                                </div>
                                <ol>
                                    <li>Прием Билета и возврат денежных средств осуществляется Оператором, если иное не будет установлено соглашением Оператором и Организатора и не будет указано на Сайте. Положения настоящей статьи в полной мере распространяются на случаи возврата Билетов, вне зависимости от места их приобретения (на Сайте или Точке продаж).</li>
                                    <li>К возврату принимаются подлинные Билеты, приобретенные Покупателем у Оператора посредством Сайта или в Точках продаж. В случае если Билет имеет физические повреждения (в том числе, но не ограничиваясь: надрывы, подчистки, отметки, исправления, надписи нанесенные не Оператором, отсутствие корешка «контроля» и т.п.), он также принимается к возврату при условии проведения проверки на подлинность такого Билета. К возврату в любом случае принимаются Билеты, действительные к проходу на Мероприятие на дату получения Оператором соответствующего заявления. Возврат Билетов на прошедшие дни проведения Мероприятия, а равно заявления на возврат, поступившие к Организатору с опозданием, не рассматриваются в связи с надлежащим исполнением Организатором обязательств в рамках Договора оказания услуг, опосредованного таким Билетов. Равным образом Оператор и/или Организатор оставляет за собой право на отказ в возврате денежных средств Покупателю в случаях, определенных положениями применимого права, в частности, в случаях злоупотребления Покупателем своими правами, а равно нарушения им условий настоящей Оферты при приобретении Билетов.</li>
                                    <li>При условии соблюдения требований пункта 2. и при условии, что прием Билета и возврат денежных средств осуществляется Оператором, денежные средства могут быть возвращены Покупателю лишь на основании его заявления, поданного Оператору в простой письменной форме с обязательным приложением оригинала Билета. Форма заявления размещается Оператором на Сайте и Точках продаж. Указанное заявление подается Покупателем лично либо лицом, действующим на основании нотариально заверенной доверенности, в Точку продаж Оператора по адресу, указанному на Сайте, с предъявлением документа, удостоверяющего личность либо путем направления пакета документов на адрес электронной почты Оператора.</li>
                                    <li>При условии соблюдения положений пункта 3, в случае если номинальная стоимость Билета  была оплачена Покупателем безналичным путем, возврат денежных средств в оговоренных пределах осуществляется лишь путем их перечисления на указанный Покупателем в заявлении банковский счет. В случае оплаты номинальной стоимости Билетов наличными денежными средствами, то они подлежат возврату в Точке продаж, указанной на Сайте.</li>
                                    <li>Возврат денежных средств за Билеты в соответствии с пунктами 1 – 4 осуществляется в  течение 10 (десяти) календарных дней, с момента получения Оператором заявления Покупателя, выполненного по форме, определенной в пункте 3.</li>
                                    <li>При возврате Билета по причине отмены, замены или переноса Мероприятия (закрытия (полного или частичного) Мероприятия в дни, на которые был приобретен Билет), а равно по причине одностороннего отказа Покупателя, Покупателю возвращается номинальная стоимость Билета.</li>
                                    <li>Денежные средства за утерянные, испорченные или неиспользованные Билеты возврату не подлежат.</li>
                                    <li>В случае отмены (замены, переноса) Мероприятия, а равно закрытия (полного или частичного) Мероприятия в дни, на которые Покупателем был приобретен Билет, осуществляется в порядке, установленном в пунктах 1 – 5 в месте и сроки, дополнительно указанные на Сайте и Точках продаж. При этом Покупатель принимает и соглашается с тем, что официальной информацией, свидетельствующей об отмене, замене или переносе Мероприятия (закрытия (полного или частичного) Мероприятия в определенные дни), является информация, размещенная на Сайте, а также в Точках продаж.</li>
                                    <li>Покупатель подтверждает и соглашается с тем, что порядок и основания возврата Билета, а также лицо, обязанное осуществлять такой возврат, до него доведены в полном объеме, ему известны и понятны.</li>
                                    <li>Возврату подлежат билеты, полученные минимум за 48 часов до начала мероприятия.</li>
                                </ol>
                                <p>Заполнненное заявление, документ, удостоверяющий личность и электронные билеты просим отправить на почтовый ящик <a href='mailto:info@kassir.kg'>info@kassir.kg</a> </p>
                                <ul>
                                    <li><a href='http://kassir.kg/document/заявление_на_возврат_денежных_средств.doc' download>Заявление на возврат денежных средств</a></li>
                                    <li><a href='http://kassir.kg/document/заяление_на_возврат_денежных_средств_за_электроный_билет.doc' download>Заявление на возврат денежных средств за электронный билет</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        );
    })

export default Return;
